module.exports = "/**\n * Setting the html and body to be 100%. It works in conjunction with PageWrapper's\n *  .page-container and .page-push style to put the footer at the bottom.\n * This is one approach to having footer stick on the bottom without resorting to\n * absolute positioning. If however, these styles needs to be removed, then the\n * footer needs to be redesigned.\n */\nhtml, body, .app-container {\n  height: 100%; }\na:-webkit-any-link {\n  color: -webkit-link;\n  text-decoration: underline; }\n.page-container {\n  margin: 1em; }\n.sr-only {\n  position: absolute;\n  width: 1px;\n  height: 1px;\n  padding: 0;\n  margin: -1px;\n  overflow: hidden;\n  clip: rect(0, 0, 0, 0);\n  border: 0; }\n.u-flr {\n  float: right; }\n.u-fll {\n  float: left; }\n.ui-g-nopad-tb {\n  padding-top: 0;\n  padding-bottom: 0; }\n.u-hright {\n  text-align: right; }\n/** This pushes the content to the bottom.\n  Used when you want to bottom align a text*/\n.u-bottom-align {\n  display: inline-block;\n  height: 100%;\n  vertical-align: bottom;\n  width: 3px;\n  margin-bottom: -3em; }\n.u-spin-icon {\n  display: inline-block;\n  -webkit-animation: doSpin 2s infinite linear;\n          animation: doSpin 2s infinite linear; }\n@-webkit-keyframes doSpin {\n  0% {\n    -webkit-transform: rotate(0);\n            transform: rotate(0); }\n  100% {\n    -webkit-transform: rotate(360deg);\n            transform: rotate(360deg); } }\n@keyframes doSpin {\n  0% {\n    -webkit-transform: rotate(0);\n            transform: rotate(0); }\n  100% {\n    -webkit-transform: rotate(360deg);\n            transform: rotate(360deg); } }\n"